import { encodeStateAsUpdate, applyUpdate, Doc } from 'yjs';
import { writeUpdate, readSyncMessage, writeSyncStep1 } from 'y-protocols/sync';
import {
  Awareness,
  encodeAwarenessUpdate,
  applyAwarenessUpdate,
  removeAwarenessStates,
} from 'y-protocols/awareness';

import {
  createEncoder,
  writeVarUint,
  toUint8Array,
  writeVarUint8Array,
  length,
} from 'lib0/encoding';
import { createDecoder, readVarUint, readVarUint8Array } from 'lib0/decoding';
import { setIfUndefined } from 'lib0/map';
import { LeveldbPersistence } from 'y-leveldb';

import debounce from 'lodash/debounce';

import { callbackHandler, isCallbackSet } from './callback';

const CALLBACK_DEBOUNCE_WAIT =
  parseInt(process.env.CALLBACK_DEBOUNCE_WAIT) || 2000;
const CALLBACK_DEBOUNCE_MAXWAIT =
  parseInt(process.env.CALLBACK_DEBOUNCE_MAXWAIT) || 10000;

const wsReadyStateConnecting = 0;
const wsReadyStateOpen = 1;
// const wsReadyStateClosing = 2;
// const wsReadyStateClosed = 3;
const messageSync = 0;
const messageAwareness = 1;
// const messageAuth = 2

// disable gc when using snapshots!
const gcEnabled = process.env.GC !== 'false' && process.env.GC !== '0';
const persistenceDir = process.env.YPERSISTENCE;

export class WSSharedDoc extends Doc {
  awareness: Awareness;

  name: string;

  conns: Map<object, Set<number>>;

  /**
   * @param {string} name
   */
  constructor(name: string) {
    super({ gc: gcEnabled });
    this.name = name;
    /**
     * Maps from conn to set of controlled user ids. Delete all user ids from awareness when this conn is closed
     * @type {Map<Object, Set<number>>}
     */
    this.conns = new Map();
    /**
     * @type {awarenessProtocol.Awareness}
     */
    this.awareness = new Awareness(this);
    this.awareness.setLocalState(null);

    const awarenessChangeHandler = (
      {
        // changes
        added,
        updated,
        removed,
      }: {
        added: Array<number>;
        updated: Array<number>;
        removed: Array<number>;
      },
      // connection that made the change
      conn: object | null
    ) => {
      const changedClients = added.concat(updated, removed);
      if (conn !== null) {
        const connControlledIDs = this.conns.get(conn);
        if (connControlledIDs !== undefined) {
          added.forEach((clientID) => {
            connControlledIDs.add(clientID);
          });
          removed.forEach((clientID) => {
            connControlledIDs.delete(clientID);
          });
        }
      }
      // broadcast awareness update
      const encoder = createEncoder();
      writeVarUint(encoder, messageAwareness);
      writeVarUint8Array(
        encoder,
        encodeAwarenessUpdate(this.awareness, changedClients)
      );
      const buff = toUint8Array(encoder);
      this.conns.forEach((_, c) => {
        send(this, c, buff);
      });
    };
    this.awareness.on('update', awarenessChangeHandler);
    this.on('update', updateHandler);
    if (isCallbackSet) {
      this.on(
        'update',
        debounce(callbackHandler, CALLBACK_DEBOUNCE_WAIT, {
          maxWait: CALLBACK_DEBOUNCE_MAXWAIT,
        })
      );
    }
  }
}

let persistence: {
  bindState: (docName: string, ydoc: WSSharedDoc) => void;
  writeState: (docName: string, ydoc: WSSharedDoc) => Promise<any>;
  provider: LeveldbPersistence;
} | null = null;
if (typeof persistenceDir === 'string') {
  console.info(`Persisting documents to "${persistenceDir}"`);
  const ldb = new LeveldbPersistence(persistenceDir);
  persistence = {
    provider: ldb,
    bindState: async (docName, ydoc) => {
      const persistedYdoc = await ldb.getYDoc(docName);
      const newUpdates = encodeStateAsUpdate(ydoc);
      ldb.storeUpdate(docName, newUpdates);
      applyUpdate(ydoc, encodeStateAsUpdate(persistedYdoc));
      ydoc.on('update', (update) => {
        ldb.storeUpdate(docName, update);
      });
    },
    writeState: async (docName, ydoc) => {
      // some empty function
    },
  };
}

export function setPersistence(
  persistence_: {
    bindState: (docName: string, ydoc: WSSharedDoc) => void;
    writeState: (docName: string, ydoc: WSSharedDoc) => Promise<any>;
    provider: LeveldbPersistence;
  } | null
) {
  persistence = persistence_;
}

export function getPersistence(): null | {
  bindState: (docName: string, ydoc: WSSharedDoc) => void;
  writeState: (docName: string, ydoc: WSSharedDoc) => Promise<any>;
} | null {
  return persistence;
}

const docs: Map<string, WSSharedDoc> = new Map();
// exporting docs so that others can use it
const _docs = docs;
export { _docs as docs };

const updateHandler = (update: Uint8Array, origin: any, doc: WSSharedDoc) => {
  const encoder = createEncoder();
  writeVarUint(encoder, messageSync);
  writeUpdate(encoder, update);
  const message = toUint8Array(encoder);
  doc.conns.forEach((_, conn) => send(doc, conn, message));
};

/**
 * Gets a Y.Doc by name, whether in memory or on disk
 *
 * docname - the name of the Y.Doc to find or create
 * gc - whether to allow gc on the doc (applies only when created)
 */
const getYDoc = (docname: string, gc = true): WSSharedDoc =>
  setIfUndefined(docs, docname, () => {
    const doc = new WSSharedDoc(docname);
    doc.gc = gc;
    if (persistence !== null) {
      persistence.bindState(docname, doc);
    }
    docs.set(docname, doc);
    return doc;
  });

const _getYDoc = getYDoc;
export { _getYDoc as getYDoc };

const messageListener = (conn: any, doc: WSSharedDoc, message: Uint8Array) => {
  try {
    const encoder = createEncoder();
    const decoder = createDecoder(message);
    const messageType = readVarUint(decoder);
    // eslint-disable-next-line default-case
    switch (messageType) {
      case messageSync:
        writeVarUint(encoder, messageSync);
        readSyncMessage(decoder, encoder, doc, conn);

        // If the `encoder` only contains the type of reply message and no
        // message, there is no need to send the message. When `encoder` only
        // contains the type of reply, its length is 1.
        if (length(encoder) > 1) {
          send(doc, conn, toUint8Array(encoder));
        }
        break;
      case messageAwareness: {
        applyAwarenessUpdate(doc.awareness, readVarUint8Array(decoder), conn);
        break;
      }
    }
  } catch (err) {
    console.error(err);
    doc.emit('error', [err]);
  }
};

const closeConn = (doc: WSSharedDoc, conn: any) => {
  if (doc.conns.has(conn)) {
    const controlledIds: Set<number> = doc.conns.get(conn);
    doc.conns.delete(conn);
    removeAwarenessStates(doc.awareness, Array.from(controlledIds), null);
    if (doc.conns.size === 0 && persistence !== null) {
      // if persisted, we store state and destroy ydocument
      persistence.writeState(doc.name, doc).then(() => {
        doc.destroy();
      });
      docs.delete(doc.name);
    }
  }
  conn.close();
};

const send = (doc: WSSharedDoc, conn: any, m: Uint8Array) => {
  if (
    conn.readyState !== wsReadyStateConnecting &&
    conn.readyState !== wsReadyStateOpen
  ) {
    closeConn(doc, conn);
  }
  try {
    conn.send(m, (err: any) => {
      // eslint-disable-next-line no-unused-expressions
      err != null && closeConn(doc, conn);
    });
  } catch (e) {
    closeConn(doc, conn);
  }
};

const pingTimeout = 30000;

export function setupWSConnection(
  conn: any,
  req: any,
  { docName = req.url.slice(1).split('?')[0], gc = true }: any = {}
) {
  // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access, no-param-reassign
  conn.binaryType = 'arraybuffer';
  // get doc, initialize if it does not exist yet
  const doc = getYDoc(docName, gc);
  doc.conns.set(conn, new Set());
  // listen and reply to events
  conn.on('message', (message: ArrayBuffer) =>
    messageListener(conn, doc, new Uint8Array(message))
  );

  // Check if connection is still alive
  let pongReceived = true;
  const pingInterval = setInterval(() => {
    if (!pongReceived) {
      if (doc.conns.has(conn)) {
        closeConn(doc, conn);
      }
      clearInterval(pingInterval);
    } else if (doc.conns.has(conn)) {
      pongReceived = false;
      try {
        conn.ping();
      } catch (e) {
        closeConn(doc, conn);
        clearInterval(pingInterval);
      }
    }
  }, pingTimeout);
  conn.on('close', () => {
    closeConn(doc, conn);
    clearInterval(pingInterval);
  });
  conn.on('pong', () => {
    pongReceived = true;
  });
  // put the following in a variables in a block so the interval handlers don't keep in in
  // scope
  {
    // send sync step 1
    const encoder = createEncoder();
    writeVarUint(encoder, messageSync);
    writeSyncStep1(encoder, doc);
    send(doc, conn, toUint8Array(encoder));
    const awarenessStates = doc.awareness.getStates();
    if (awarenessStates.size > 0) {
      const encoder = createEncoder();
      writeVarUint(encoder, messageAwareness);
      writeVarUint8Array(
        encoder,
        encodeAwarenessUpdate(doc.awareness, Array.from(awarenessStates.keys()))
      );
      send(doc, conn, toUint8Array(encoder));
    }
  }
}
